import React, { Component } from "react";
import Layout from "../components/Layout";

class Cart extends Component {
  state = {
    totalPriceCart: 0,
    vendorBjt: [ ],
    vendorRinci: [ ]
  };

  // caluclate totalPriceCart function (array is a parameter for this.props.productsInCart)
  calculateTotalPrice = array => {
    let totalPricesArray = [];
    array.forEach(product => {
      let productPrice = parseInt(product.price) * product.quantity;
      totalPricesArray.push(productPrice);
    });
    let totalPriceCart = totalPricesArray.reduce((a, b) => a + b, 0);
    this.setState({
      totalPriceCart: totalPriceCart
    });
  };

  // on page load caluclate price 
  componentDidMount() {
    this.calculateTotalPrice(this.props.productsInCart);
    this.filterVendorRinci()
    this.filterVendorBjt("Bjt")
  }

  // if a product is removed update totalPriceCart  
  componentDidUpdate(prevProps) {
    if (this.props !== prevProps) {
      this.calculateTotalPrice(this.props.productsInCart);
      this.filterVendorRinci()
      this.filterVendor("Bjt")
    } 
    
  }

  filterVendorRinci() { 
    let vendorRinciProducts = this.props.productsInCart.filter(product => {
      return product.vendor_name == "Rinci"
    })
    let vendorRinci = vendorRinciProducts
    
    this.setState({
      vendorRinci: vendorRinci
    })

  }


  // filterVendorBjt() { 
  //   let vendorBjtProducts = this.props.productsInCart.filter(product => {
  //     return product.vendor_name == "Bjt"
  //   })
  //   let vendorBjt = vendorBjtProducts
    
  //   this.setState({
  //     vendorBjt: vendorBjt
  //   })
   
  // }

  filterVendor(vendorName) { 
    let vendorBjtProducts = this.props.productsInCart.filter(product => {
      return product.vendor_name == vendorName
    })
    let vendorBjt = vendorBjtProducts
    
    this.setState({
      vendorBjt: vendorBjt
    })
   
  }



  render() {
    {console.log(this.state)}
    return (
      <Layout>
        {this.props.productsInCart.length > 0 ? (
          <div>
            {this.props.productsInCart.map((product, index) => {
              return (
                <div index={product.id}>
                  <p>
                    <strong>Product:</strong>
                    {product.name}
                  </p>
                  <p>
                    <strong>Quantity:</strong>
                    {product.quantity}
                  </p>
                  <p>
                    <strong>Total: </strong>
                    {product.quantity * parseInt(product.price)}€
                  </p>

                  <button onClick={() => this.props.deleteProduct(product.id)}>
                    remove product
                  </button>
                </div>
              );
            })}
            <h2>Total Price Cart: {this.state.totalPriceCart}€</h2>
          </div>
        ) : (
          <p>no product in cart</p>
        )}

        
      </Layout>
    );
  }
}

export default Cart;
